
<template >
    <section>
        <div class="col-md-12 p-0 mt-2">
            <div class="theme-primary partition-full">
                <span class="p-name text-white">Timesheet</span>
                <span class="p-actions float-right">
                    <a id="AddManually" href="javascript:;"
                        class="p-action-btn text-white addman btn-info"><span>Expert</span></a>
                    <a id="AddManually" href="javascript:;" class="p-action-btn text-white addman btn-info"><span>This
                            week</span></a>
                    <a id="AddManually" href="javascript:;" class="p-action-btn text-white addman btn-info"><i
                            class="fa fa-delete mx-2"></i></a>
                </span>
            </div>
        </div>
        <div class="listing py-3 px-3">
            <!-- <div>
                <label>...To</label><br>
                    <input id="txtToDate_" name="endDate" type="date" class="textfield2 ie_crossicon_none ToDate form-control p-1 hasdatetimepicker notvue" autocomplete="off" data-toggle="datetimepicker" title="Select Date" placeholder="M/D/YYYY" v-model="To">
            </div>
             -->
            <dynamic-form lang="en" v-if="DataLoaded" :buttons="buttons1" :schema="FormSchema" v-on:OnSubmit="onSubmit">
                <template v-slot:tgslot-slotdate="{ data }">
                    <div class="col-12 input-group timesheetdatepicker bg-white border-0 p-0">
                        <div class="form-control">
                            <label>DateRange:</label>&nbsp;<a id="aDateLable" class="pr-2 text-dark"
                                style="cursor:default">{{ DisplayWeekDate }}</a>
                            <input id="date_range" v-model="dateselect" value="2023/12/12 - 2023/12/12" name="date_range"
                                type="date" @input="GetStartEndDates"
                                class="textfield2 ie_crossicon_none ToDate form-control p-1 hasdatetimepicker notvue"
                                autocomplete="off" data-toggle="datetimepicker" title="Select Date"
                                placeholder="Date Range">
                        </div>
                    </div>

                </template>

                <template v-slot:tgslot-slotbutton="{ data }">
                    <button class="btn btn-success" @click="FetchData">{{ $t('ShowTimeSheet') }}</button>
                </template>
                <template v-slot:tgslot-slotstartenddate="{ data }">
                    <p style="top:-74px;margin-left: 104px;">{{ DisplayWeekDate }}</p>
                </template>
            </dynamic-form>
            <dynamic-form lang="en" v-if="DataLoaded" :buttons="buttons" :schema="ListSchema" v-on:OnSubmit="onSubmit">
                <template v-slot:tgslot-TimeSheetTable="{ data }">
                    <div class="listing">
                        <div class="col-12 mb-2">
                            <div class="mr-2 d-inline-block">
                                <span class="legends-purple colorboxsquare"></span>Auditable Records
                            </div>
                            <div class="mr-2 d-inline-block">
                                <span class="legends_low colorboxsquare"></span>Unpaid / Leave Records
                            </div>
                            <div class="mr-2 d-inline-block">
                                <span class="legends_medium colorboxsquare"></span>TimeSheet
                            </div>
                            <div class="mr-2 d-inline-block">
                                <span class="legends_profile colorboxsquare"></span>Leave / Holiday
                            </div>
                            <div class="mr-2 d-inline-block">
                                <span class="legends_high colorboxsquare"></span>Holiday / Leave Auditable Records
                            </div>
                            <div class="mr-2 d-inline-block">
                                <span class="lg-pausedresumed colorboxsquare"></span>Paused / Resumed Track Entries
                            </div>
                        </div>
                    </div>
                    <tg-list :RenderRowActionMethod="renderActions" IdentityColumn="ProjectId" ModuleName="TimeTrack"
                        :HeaderText="$t('Brand')" SubModuleCode="Timesheet" :LegendArray="LegendArray" :SortExp="SortExp"
                        :SortBy="SortBy" :IsShowAction="true" :ListData="TimesheetList" :HeaderData="Headers"
                        :showCheckBox="true" :NorecordfoundText="$t('NoRecordfound')"
                        v-on:ActionButtonClick="actionButtonClick">
                        <template v-slot:slotdata="{ data }">
                            <template>
                                <input v-model="data.row[data.column.COLUMN_NAME]">
                            </template>

                        </template>
                    </tg-list>
                </template>
            </dynamic-form>



            <!-- <a id="btnCancel" title="Cancel" class="btn btn-danger d-none"><i class="fa fa-close pr-2"></i><span>Cancel</span></a> -->
            <!-- <a id="btnCopyLastWeek" isapprover="0" class="btn btn-warning formbtn mr-2 text-dark" onclick="" name="" title='Copy to other Users'><i class="fa fa-copy pr-2"></i>Copy to other Users</a>
                        <a id="btnCopyLastWeek" isapprover="0" class="btn btn-warning formbtn mr-2 text-dark" onclick="" name="" title='Copy Last week'><i class="fa fa-copy pr-2"></i>Copy Last week</a> -->
            <a id="btnSaveAsDraft" isapprover="0" class="btn btn-warning formbtn text-dark mr-2"
                v-on:click="SaveBonus(1, 0)" name="" title='SaveAsDraft'><i class="fa fa-save pr-2"></i>SaveAsDraft</a>
            <!-- <a id="btnSendForApproval" isapprover="0" class="btn btn-success text-white formbtn mr-2" onclick="" title='SendForApproval'><i class="fa fa-send pr-2"></i>SendForApproval</a> -->
        </div>


    </section>
</template>
<script>
import DataService from '../../services/DataService';
import { getPreviousDay } from "@/assets/common/vue-common.js"
export default
    {
        data() {
            return {
                getPreviousDay,
                dateselect: '',
                FormSchema: [
                    {
                        layoutType: "triple",
                        Data: [
                            // {
                            //     //     astype: "DateField",
                            //     // label: this.$t('StartDateTime'),
                            //     // name: "StartDate",
                            //     // value: '',
                            //     // placeholder: "",
                            //      //mode:Range.END_TO_START,
                            //     // mode: "dateTime",
                            //     astype: "DateField",
                            //     label: "Date Range:",
                            //     name: "date_range",
                            //     value: '',
                            //     // id: "week-picker",
                            //     placeholder: "This Week",
                            //     //validationRules: "required",
                            //     config: {
                            //         mode: "dateTime",
                            //         is24hr: true,
                            //         format: "MM-dd-yyy",
                            //         //minDate: (new Date()),
                            //         onBlur: this.GetStartEndDates
                            //     }
                            // },
                            {
                                astype: "SlotField",
                                label: "",
                                name: "slotdate",
                                value: "",
                                placeholder: "Date Range",
                            },
                            {
                                astype: "SelectField",
                                label: "User",
                                name: "user_id",
                                value: "",
                                placeholder: "",
                                config: {
                                    options: [],
                                },
                                validationRules: {
                                    //"required": true
                                },
                            },
                            {
                                astype: "SlotField",
                                label: "",
                                name: "slotbutton",
                                value: "",
                                placeholder: "",

                            },
                            // {
                            //     astype: "SlotField",
                            //     label: "",
                            //     name: "slotstartenddate",
                            //     value: "",
                            //     placeholder: "",

                            // },
                        ]
                    }
                ],
                // buttons1: [
                //     {
                //         type: "submit",
                //         class: "btn btn-success",
                //         text: "<i class='fa pr-2'></i>Show Time Sheet",
                //         isVisible: true,
                //     }
                // ],
                ListSchema: [
                    {
                        layoutType: "single",
                        Data: [
                            {
                                astype: "SlotField",
                                label: "",
                                name: "TimeSheetTable",
                                value: "",
                                placeholder: "",
                                config: {
                                    options: [],
                                },
                                validationRules: {
                                    //"required": true
                                },
                            },

                        ]
                    }
                ],
                buttons: [
                    {
                        type: "submit",
                        class: "btn btn-warning formbtn mr-2 text-dark",
                        text: "<i class='fa fa-copy pr-2'></i>Copy Last Week",
                        //click: SaveBonus(1,0),
                        isVisible: true,
                    },
                    {
                        type: "submit",
                        class: "btn btn-warning formbtn mr-2 text-dark",
                        text: "<i class='fa fa-save pr-2'></i>Save As Draft",
                        callbackfunction: 'SaveBonus(1,0)',
                        isVisible: true,
                        //OnChange:this.SaveBonus(1,0)
                    },
                    {
                        type: "submit",
                        class: "btn btn-success text-white formbtn mr-2",
                        text: "<i class='fa fa-save pr-2'></i>Send For Approval",
                        isVisible: true,

                    }
                ],
                listActions: [
                    {
                        title: 'Delete',
                        icon: 'fa fa-trash',
                        callbackfunction: 'DeleteData',
                        additionalSpan: '',
                        isVisible: true
                    },
                ],
                LegendArray: [{
                    class: '',
                    backgroundColor: 'rgb(245 234 222)',
                    name: 'Recurring Tickets'
                }, {
                    class: '',
                    backgroundColor: 'rgb(245 234 222)',
                    name: 'Recurring Tickets'
                }
                ],
                DataLoaded: 0,
                PageNumber: 1,
                PageSize: 10,
                SortBy: '',
                SortExp: '',
                noRecord: false,
                TotalRecords: 0,
                isLoading: false,
                TimesheetList: [],
                UserId: null,
                StartDate: null,
                EndDate: null,
                DisplayWeekDate: "This Week",
                StartWeekDay: null,
                Headers: [
                    //     {
                    //     "COLUMN_NAME": "ProjectName",
                    //     "DATA_TYPE": "string",
                    //     "SORTABLE": false,
                    //     "VISIBLE": true,
                    //     "DISPLAY_NAME": "Projects",
                    //     "DISPLAY_ORDER": 1
                    // },
                    // {
                    //     "COLUMN_NAME": "break_tt",
                    //     "DATA_TYPE": "text",
                    //     "SORTABLE": false,
                    //     "VISIBLE": true,
                    //     "DISPLAY_NAME": "Mon, Dec",
                    //     "DISPLAY_ORDER": 2,
                    // },
                ],
            }
        },
        created: async function () {
            var VueObj = this;
            this.DataLoaded = 1;
            //VueObj.GetStartEndDates("thisweek");
            //RdatePickerForWeekRange('week-picker');
            await VueObj.GetStartEndDatesNew();
            await VueObj.GetUsersForTimesheet();
            VueObj.FormSchema[0].Data[1].value = VueObj.GetUserInfo.ID;
            VueObj.FetchData();
        },
        mounted: function () {
            var VueObj = this;
            // VueObj.RdatePickerForWeekRange('week-picker');
            // VueObj.RdatePickerForWeekRange('date-range');
            //GetStartEndDates("thisweek");
            //VueObj.GetStartEndDates("thisweek");
            console.log('mounted')
        },
        methods: {
            async GetUsersForTimesheet() {
                var VueObj = this;
                VueObj.isLoading = true;
                var url = `companyId=${VueObj.GetUserInfo.CompanyId}&loggedUserId=${VueObj.GetUserInfo.ID}&userType=CA&userId=${VueObj.FormSchema[0].Data[1].value}&moduleName=`;
                await DataService.GetUsersForTimesheet(url)
                    .then(function (response) {

                        response.data.result.forEach(item => {
                            VueObj.obj = {
                                name: item.username,
                                value: item.useR_ID
                            }
                            VueObj.FormSchema[0].Data[1].config.options.push(VueObj.obj);
                        });

                        VueObj.isLoading = false;
                    });
            },
            OnChange: function () {
                var checkCount = $('.chkItems:checked').not("[id^='chkAll']").length;
                if (checkCount > 0) {
                    $('#DeleteMultiple').removeClass('disabled');
                }
                else {
                    $('#DeleteMultiple').addClass('disabled');
                }
            },
            commonHeaderButtonClick: function (event) {


            },
            async onSubmit(formfields, e) {
                var vm = this;
                vm.FetchData();
            },
            RdatePickerForWeekRange: async function (dateFrom) {


            },
            GetStartEndDates: async function (event, id, a) {
                console.log("date", this.dateselect)
                var VueObj = this;
                let url = `DateSpan=thisweek&report=`;
                await DataService.GetStartEndDate(url).then(response => {
                    VueObj.StartDate = response.data.dateStart;
                    VueObj.EndDate = response.data.dateEnd;
                    VueObj.DisplayWeekDate = "";
                    VueObj.DisplayWeekDate = response.data.displayText;
                    VueObj.StartWeekDay = response.data.startWeekDay;
                });
                //VueObj.FormSchema[0].Data[0].placeholder = "Date Range: " + VueObj.DisplayWeekDate;
                var offset = 1;
                switch (VueObj.StartWeekDay.toLowerCase()) {
                    case 'tuesday':
                        offset = 2;
                        break;
                    case 'wednesday':
                        offset = 3;
                        break;
                    case 'thursday':
                        offset = 4;
                        break;
                    case 'friday':
                        offset = 5;
                        break;
                    case 'satureday':
                        offset = 6;
                        break;
                    case 'sunday':
                        offset = 0;
                        break;
                    default:
                        offset = 1;
                        break;
                }


                // switch (VueObj.StartWeekDay) {
                //     case "wednesday":
                //         offset = 4;
                //         break;
                //     case "sm":
                //         limitSize = 1;
                //     case "md":
                //         limitSize = 2;
                //     case "lg":
                //         limitSize = 3;
                //     case "xl":
                //         limitSize = 3;
                // }


                var today = new Date(this.dateselect);
                if (today == '') {
                    today = new Date();
                }
                var currentDay = today.getDay() - offset;
                var startdate = new Date(today.getFullYear(), today.getMonth(), today.getDate() - currentDay);
                var enddate = new Date(today.getFullYear(), today.getMonth(), today.getDate() - currentDay + 6);
                var dateTime = VueObj.$options.filters.formatDate(startdate) + '-' + VueObj.$options.filters.formatDate(enddate);

                VueObj.StartDate = VueObj.$options.filters.formatDate(startdate);
                VueObj.EndDate = VueObj.$options.filters.formatDate(enddate);
                VueObj.DisplayWeekDate = "";
                VueObj.DisplayWeekDate = dateTime;

                //VueObj.FetchData();

            },
            GetStartEndDatesNew: async function (thisweek) {
                var VueObj = this;
                let url = `DateSpan=thisweek&report=`;
                await DataService.GetStartEndDate(url).then(response => {
                    VueObj.StartDate = response.data.dateStart;
                    VueObj.EndDate = response.data.dateEnd;
                    VueObj.DisplayWeekDate = response.data.displayText;
                    VueObj.StartWeekDay = response.data.startWeekDay;
                });
                VueObj.FormSchema[0].Data[0].placeholder = "Date Range: " + VueObj.DisplayWeekDate;
                VueObj.DisplayWeekDate = "This Week";

            },
            FetchData: async function () {
                var VueObj = this;
                VueObj.isLoading = true;
                var fDate = VueObj.StartDate;
                var tDate = VueObj.EndDate;

                // var url = `userId=${VueObj.GetUserInfo.ID}&dateFrom=${fDate}&dateTo=${tDate}&userId=${VueObj.FormSchema[0].Data[1].value}&companyId=270936`;
                var url = `companyId=${VueObj.GetUserInfo.CompanyId}&userId=${VueObj.FormSchema[0].Data[1].value}&dateFrom=${fDate}&dateTo=${tDate}`;
                await DataService.ListTimesheetForWeek(url).then(response => {
                    if (response.data != null && response.data != "" && response.data.length > 0) {
                        // const headerList=Object.keys(response.data[0]).filter(key => key !== 'ProjectId' && key !== 'ProjectName' && key !== 'TaskId' && key !== 'TaskDetail');
                        const headerList = Object.keys(response.data[0]);
                        VueObj.isLoading = false;
                        var count;
                        this.Headers = [];
                        headerList.forEach(item => {
                            count++;
                            if (item == "ProjectId" || item == "TaskId") {
                                this.Headers.push({
                                    "COLUMN_NAME": item,
                                    "DATA_TYPE": "string",
                                    "SORTABLE": false,
                                    "VISIBLE": false,
                                    "DISPLAY_NAME": item,
                                    "DISPLAY_ORDER": count,
                                });
                            }
                            else if (item == "TaskDetail") {
                                // this.Headers.push({
                                //     "COLUMN_NAME": item,
                                //     "DATA_TYPE": "string",
                                //     "SORTABLE": false,
                                //     "VISIBLE": true,
                                //     "DISPLAY_NAME": item,
                                //     "DISPLAY_ORDER": count,
                                // });
                            }
                            else if (item == "ProjectName") {
                                this.Headers.push({
                                    "COLUMN_NAME": item,
                                    "DATA_TYPE": "string",
                                    "SORTABLE": false,
                                    "VISIBLE": true,
                                    "DISPLAY_NAME": "Projects",
                                    "DISPLAY_ORDER": 1
                                });
                            }
                            else {
                                this.Headers.push({
                                    "COLUMN_NAME": item,
                                    "DATA_TYPE": "text",
                                    "SORTABLE": false,
                                    "VISIBLE": true,
                                    "DISPLAY_NAME": getPreviousDay(item),
                                    "DISPLAY_ORDER": count,
                                });
                            }
                        });

                        // headerList.forEach((item) => {
                        //     VueObj.Headers.forEach(column => {
                        //         column.settings = null;

                        //         if (column.COLUMN_NAME == item && column.COLUMN_NAME !=='ProjectName' && item !=='ProjectName') {
                        //             column.settings = {
                        //                 //isInSlot: true
                        //             }
                        //         }
                        //     }
                        //     );
                        // });

                        VueObj.Headers.forEach((column) => {
                            column.settings = null;
                            headerList.forEach(item => {

                                if (column.COLUMN_NAME == item && column.COLUMN_NAME !== 'ProjectName' && column.COLUMN_NAME !== 'TaskDetail') {
                                    column.settings = {
                                        isInSlot: true
                                    }
                                }
                            }
                            );
                        });

                        VueObj.TotalRecords = response.data.length;
                        VueObj.TimesheetList = response.data;

                        VueObj.TotalPages = 1;
                        VueObj.CurrentPage = 1;
                        $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                        $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                        VueObj.noRecord = false;

                        $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                        $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                        VueObj.noRecord = false;
                    }
                    else {
                        VueObj.noRecord = true;
                        VueObj.TimesheetList = [];
                    }

                });
            },
            SingleDeleteData: function (item) {
                var VueObj = this;
                var fDate = VueObj.StartDate;
                var tDate = VueObj.EndDate;
                VueObj.confirmR(VueObj.$t("ConfirmDelete"), true, (VueObj.$t("Delete") + "  " + VueObj.$t("projectId")), function () {
                    let url = `companyId=${VueObj.GetUserInfo.CompanyId}&userId=${VueObj.FormSchema[0].Data[1].value}&projectId=${item.ProjectId || '0'}&taskId=${item.TaskId || '0'}&dateFrom=${fDate}&dateTo=${tDate}`;
                    DataService.DeleteTrackEntryByProjectAndTaskId(url).then(response => {
                        if (response.data == '1') {
                            VueObj.ShowAlert(VueObj.$t('DeletedSuccessfully'), "Success", true, VueObj.$t('Alert'));

                            VueObj.FetchData();
                        } else {
                            VueObj.ShowAlert(VueObj.$t('DeletedError', [VueObj.$t('val')]), "failure", true, VueObj.$t('Alert'));
                        }
                    });
                });
            },
            actionButtonClick(callbackfunction, item) {
                var vm = this;
                switch (callbackfunction) {
                    case 'Editdata':
                        this.EditAssignCategoryClick(item)
                        break;
                    case 'DeleteData':
                        vm.SingleDeleteData(item)
                        break;
                }
            },
            renderActions: function (row) {
                var self = this;
                let rtnActionArray = [];
                self.listActions.forEach(actionItem => {
                    let action = {
                        ...actionItem
                    };
                    switch (action.title) {
                        case 'Edit':
                            action.isVisible = true;
                            break;
                        case 'Delete':
                            action.isVisible = true;
                            break;
                        default:
                            action.isVisible = true;
                            break;
                    }
                    rtnActionArray.push(action);
                });
                return rtnActionArray;
            },
            SaveBonus: async function (isDraft, isSendForApproval) {
                debugger
                var VueObj = this;
                //let url = `jsonData=${JSON.stringify(VueObj.TimesheetList)}&userId=${VueObj.FormSchema[0].Data[1].value}&dateFrom=${VueObj.StartDate}&dateTo=${VueObj.EndDate}`;
                //let url = `${JSON.stringify(VueObj.TimesheetList)}&userId=${VueObj.FormSchema[0].Data[1].value}&dateFrom=${VueObj.StartDate}&dateTo=${VueObj.EndDate}`;
                 let obj = {
                    TimesheetList:VueObj.TimesheetList,
                    userId: VueObj.FormSchema[0].Data[1].value,
                    dateFrom:VueObj.StartDate,
                    dateTo:VueObj.EndDate
                 }
                 var formData = new FormData();
                 formData.append("jsonData", JSON.stringify(obj));
            //await DataService.ManageCategory(obj).then(response => {

                await DataService.SaveTimeSheetEntry(obj).then(response => {
                    console.log('SaveBonus', response);
                });
            }
        }
    }
</script>
